body {
  background: #eee;
  font-family: 'Hanalei Fill', cursive;
  /*color: red;*/
}

.App h1{
  color: red;
  text-align: center;
}

.App{
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 60%;
  height: 100%;
}

#book-list{
  padding: 0;
}
#book-list li{
  display: inline-block;
  margin: 12px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #880e4f;
  -webkit-box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
          box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
  cursor: pointer;
  color: #880e4f;
}
#title-field{
  text-align: center;
}

#add-book{
  background: #fff;
  padding: 20px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 400px;
}

#add-book .field{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

#add-book label{
  text-align: right;
  padding: 6px;
}

#add-book select, #add-book input{
  margin: 4px;
  padding: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#add-book button{
  border-radius: 50%;
  color: #fff;
  font-size: 2em;
  border: 0;
  background: #ad1457;
  padding: 0 10px;
  cursor: pointer;
  position: absolute;
  bottom: 10px;
  left: 10px;
}
#add-author{
  background: #fff;
  padding: 20px;
  position: fixed;
  right: 40%;
  bottom: 40px;
  width: 400px;
}

#add-author .field{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

#add-author label{
  text-align: right;
  padding: 6px;
}

#add-author select, #add-author input{
  margin: 4px;
  padding: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#add-author button{
  border-radius: 50%;
  color: #fff;
  font-size: 2em;
  border: 0;
  background: #ad1457;
  padding: 0 10px;
  cursor: pointer;
  position: absolute;
  bottom: 10px;
  left: 10px;
}


#book-details{
  position: fixed;
  width: 40%;
  top:0;
  right:0;
  height: 100%;
  background: #ad1457;
  padding: 30px;
  overflow: auto;
  -webkit-box-shadow: -2px -3px 5px rgba(0,0,0,0.3);
          box-shadow: -2px -3px 5px rgba(0,0,0,0.3);
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  color: #fff;
}


